<template>
<!-- 笔记列表页面 -->
  <div class="notesBox">
    <ul class="notesList">
      <p class="title">学习笔记</p>
      <!-- item.name 是组件对象需要在获取里面name -->
      <li
        v-for="item in article"
        :key="item.id"
        @click="intoDetails(item.name.name , item.date , item.type)"
      >
        <p>{{ item.describe }}</p>
        <p>记录时间：{{ item.date }}</p>
      </li>
    </ul>
  </div>
</template>
<script>
// 导入文章列表
import { components } from "../article/index";
export default {
  data() {
    return {
      article: components,
    };
  },
  methods: {
    // 点击文章，传递name进入详情页面
    intoDetails(name , date ,tag) {
      // url中显示参数跳转
      this.$router.push({ name: "details", query: { article: name ,date:date , tag:tag}});
    },
  },
};
</script>
<style lang="less" scoped>
ul,
li,
p {
  padding: 0;
  margin: 0;
  list-style: none;
}
.notesBox {
  overflow: auto;
  height: 100%;
  .notesList {
    margin-top: 30px;
    padding: 20px 40px;
    .title {
      position: absolute;
      top: 10px;
      left: 0;
      width: 100%;
      padding: 0 50px;
      box-sizing: border-box;
      line-height: 50px;
      font-style: italic;
      font-size: 20px;
      background: rgb(226, 232, 255);
    }
    li {
      padding: 10px;
      transition: 0.3s;
      border-bottom: 1px solid #ddd;
      p:nth-child(1) {
        font-size: 16px;
        line-height: 40px;
      }
      p:nth-child(2) {
        font-size: 12px;
        color: rgb(146, 146, 146);
      }

      &:hover {
        cursor: pointer;
        color: rgb(0, 60, 255);
      }
    }
  }
}
</style>
